.graph {
    position: relative;
    border-style: solid;
    border-color: #DADADB;
}

.graph > .info {
    position: absolute;
    top: 0;
    left: 62vw;
    width: 200px;
    height: fit-content;
    border-style: solid;
    border-color: #99BEEB;
    border-radius: 5px;
    padding: 15px;
}

.intro-text {
    width: 70%;
}

hr {
    border: 2px solid #F3723F;
}