h1,h2,h3,h4,h5,h6,h7, p, table, li {
  color: #333333;
}
.Logo {
  width: 200px;
}
.Body {
  padding: 64px 30vw 100px 10vw;
}
.table {
  position: relative;
}
.table::after {
  content: "Hover over a column name for a description";
  position: absolute;
  top: 0;
  left: 62vw;
  width: 200px;
  height: fit-content;
  border-style: solid;
  border-color: #99BEEB;
  padding: 15px;
  border-radius: 5px;
}


tr > th:hover::before {
  position: absolute;
  width: 200px;
  background: white;
  top: 100px;
  border-style: solid;
  border-color: #99BEEB;
  padding: 15px;
  left: 62vw;
  border-radius: 5px;
}
tr > th:nth-child(1):hover::before {
  content: "Unique ID for a comment in a post";
}
tr > th:nth-child(2):hover::before {
  content: "What the post links to. If the comment is a reply to another comment then this field will be equal to that comment id with the prefix t1_ otherwise it will link to the post id with the prefix t3_";
}
tr > th:nth-child(3):hover::before {
  content: "Uniqe ID of which post the comment belongs to";
}
tr > th:nth-child(4):hover::before {
  content: "Sum of upvotes and downvotes for the specific comment";
}
tr > th:nth-child(5):hover::before {
  content: "Username/author of the comment";
}
tr > th:nth-child(6):hover::before {
  content: "The comment itself";
}
tr > th:nth-child(7):hover::before {
  content: "When the comment was created, in UTC time";
}
